import React from "react";
import { Link } from "react-router-dom";
import ghidorahList from "../../lists/ghidorahList";

const GhidorahListDisplay = () => {
  const ghidorahListMap = ghidorahList.map((ghidorah) => {
    return (
      <li>
        <Link to={"/ghidorah" + ghidorah.link}>{ghidorahList.name}</Link>
      </li>
    );
  });

  return (
    <>
      <h2>Ghidorah</h2>
      <ul>{ghidorahListMap}</ul>
    </>
  );
};

export default GhidorahListDisplay;
