import React from "react";
import { Link } from "react-router-dom";
import godzillaList from "../../lists/godzillaList";

const GodzillaListDisplay = () => {
  const godzillaListMap = godzillaList.map((godzilla) => {
    return (
      <li>
        <Link to={"/godzilla-list" + godzilla.link}>{godzilla.name}</Link>
      </li>
    );
  });

  return (
    <div className="kaiju-list">
      <ul>{godzillaListMap}</ul>
    </div>
  );
};

export default GodzillaListDisplay;
